:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.gallery__item {
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 0;
  pointer-events: none;
  background-color: rgba(0, 0, 0, .5);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

body.modal-open {
  overflow: hidden;
}

body.modal-open .backdrop {
  opacity: 1;
  pointer-events: all;
}

.selectedImageModal {
  width: 80%;
  max-height: 100vh;
  text-align: center;
  background-color: #f0f8ff;
  padding: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.selectedImageModal__image {
  max-width: 80%;
  max-height: 90vh;
}

.closeButton {
  min-height: 30px;
  background-color: rgba(0, 0, 0, 0);
  background-image: url("closeButton.e009a741.svg");
  background-position: center;
  background-repeat: no-repeat;
  border: none;
  position: absolute;
  top: 10px;
  right: 10px;
}

